import Noticia from "./Noticia";

const Noticias = (props) => {

    console.log(props.noticias)
    const noticias =props.noticias;
    
    return (  
        <div className="card-columns">
            {Object.keys(noticias).map(key=>(
                <Noticia 
                key={key}
                noticia={noticias[key]}
                />
            ))}
            
        </div>
    );
}
 
export default Noticias;